import { LogLevel } from "./LogLevel"

if (undefined === window.console) {
    window.console = {
        error: function () {
        },
        warn: function () {
        },
        info: function () {
        },
        log: function () {
        }
    };
}
else {
    if (undefined === window.console.error) {
        window.console.error = window.console.log;
    }
    if (undefined === window.console.warn) {
        window.console.warn = window.console.log;
    }
    if (undefined === window.console.info) {
        window.console.info = window.console.log;
    }
}

/**
 * 日志辅助类。
 * 
 * @class
 */
export const Logger = {

    /**
     * 是否激活记录日志。
     * @type {boolean}
     */
    enabled: true,

    /**
     * 当前日志等级。
     * @type {number}
     */
    level: LogLevel.INFO,

    /**
     * 日志管理器是否设置为 DEBUG 等级。
     *
     * @returns {boolean} 如果当前日志等级为 DEBUG 返回 {@linkcode true} 。
     */
    isDebugLevel: function () {
        return (this.level == LogLevel.DEBUG);
    },

    /**
     * 打印 DEBUG 级别日志。
     * 
     * @param {string} tag 日志标签。
     * @param {string} text 日志内容。
     */
    d: function (tag, text) {
        if (!this.enabled || this.level > LogLevel.DEBUG) {
            return;
        }

        let buf = [ this._formatTime(), " [DEBUG] ", tag, " - ", text ];
        window.console.log(buf.join(''));
        buf = null;
    },

    /**
     * 打印 INFO 级别日志。
     * 
     * @param {string} tag 日志标签。
     * @param {string} text 日志内容。
     */
    i: function (tag, text) {
        if (!this.enabled || this.level > LogLevel.INFO) {
            return;
        }

        let buf = [ this._formatTime(), " [INFO]  ", tag, " - ", text ];
        window.console.info(buf.join(''));
        buf = null;
    },

    /**
     * 打印 WARNING 级别日志。
     * 
     * @param {string} tag 日志标签。
     * @param {string} text 日志内容。
     */
    w: function (tag, text) {
        if (!this.enabled || this.level > LogLevel.WARNING) {
            return;
        }

        let buf = [ this._formatTime(), " [WARN]  ", tag, " - ", text ];
        window.console.warn(buf.join(''));
        buf = null;
    },

    /**
     * 打印 ERROR 级别日志。
     * 
     * @param {string} tag 日志标签。
     * @param {string} text 日志内容。
     */
    e: function (tag, text) {
        if (!this.enabled || this.level > LogLevel.ERROR) {
            return;
        }

        let buf = [ this._formatTime(), " [ERROR] ", tag, " - ", text ];
        window.console.error(buf.join(''));
        buf = null;
    },

    /**
     * 打印指定级别的日志。
     * 
     * @param {string} tag 日志标签。
     * @param {string} text 日志内容。
     * @param {LogLevel} level 日志等级。
     */
    log: function (tag, text, level) {
        if (!this.enabled) {
            return;
        }

        switch (level) {
            case LogLevel.DEBUG:
                this.d(tag, text);
                break;
            case LogLevel.INFO:
                this.i(tag, text);
                break;
            case LogLevel.WARNING:
                this.w(tag, text);
                break;
            case LogLevel.ERROR:
                this.e(tag, text);
                break;
            default:
                let buf = [ this._formatTime(), " [LOG] ", tag, " - ", text ];
                window.console.log(buf.join(''));
                buf = null;
                break;
        }
    },

    /**
     * 格式化时间。
     * @private
     */
    _formatTime: function () {
        let date = new Date();
        let ret = [date.getHours(), ":", date.getMinutes(), ":", date.getSeconds(), ".", date.getMilliseconds()];
        date = null;
        return ret.join('');
    }
}
